.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 40px 20px;
    min-height: calc(100vh - 80px);
    background-color: #fafafa;
  }
  
  h2 {
    font-size: 30px;
    color: #2c3e50;
    margin-bottom: 30px;
    text-align: center;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 100%;
    max-width: 400px;
  }
  
  label {
    display: flex;
    flex-direction: column;
    font-size: 16px;
    color: #333;
  }
  
  input {
    padding: 12px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 8px;
    margin-top: 8px;
    transition: border-color 0.3s ease;
  }
  
  input:focus {
    border-color: #3498db;
    outline: none;
  }
  
  button {
    padding: 12px;
    font-size: 16px;
    background-color: #2ecc71;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #27ae60;
  }
  
 
  @media (max-width: 480px) {
    h2 {
      font-size: 22px;
    }
  
    input,
    button {
      font-size: 14px;
      padding: 10px;
    }
  }
  